import React from 'react'

class Footer extends React.Component {
    render() {
        return (
            <div id="footer">
                <div className="inner">
                    <ul className="icons">
                        <li><a href="https://twitter.com/jakeduchen" target="-blank" className="icon fa-twitter"><span className="label">Twitter</span></a></li>
                        <li><a href="https://github.com/fatcatdog" target="-blank" className="icon fa-github"><span className="label">Github</span></a></li>
                        <li><a href="https://www.linkedin.com/in/jake-duchen-05080753/" target="-blank" className="icon fa-linkedin"><span className="label">Linkedin</span></a></li>
                    </ul>
                </div>
            </div>
        )
    }
}

export default Footer



